import React, { createContext, useContext, useMemo, useState } from "react";
import { BrowserRouter, Routes, Route, Link, useNavigate } from "react-router-dom";
import { ShoppingCart, Minus, Plus, Trash2, Leaf } from "lucide-react";
import { motion } from "framer-motion";

// ---------- Mock Data ----------
const PRODUCTS = [
  { id: "snake", name: "Snake Plant", price: 12.99, category: "Low Light",
    img: "https://images.unsplash.com/photo-1614594854544-9e48b7ebe97a?q=80&w=800&auto=format&fit=crop" },
  { id: "pothos", name: "Golden Pothos", price: 9.99, category: "Trailing",
    img: "https://images.unsplash.com/photo-1600861194942-f883de0dfe96?q=80&w=800&auto=format&fit=crop" },
  { id: "monstera", name: "Monstera Deliciosa", price: 24.0, category: "Statement",
    img: "https://images.unsplash.com/photo-1614594854361-9a8f2b1e7a6c?q=80&w=800&auto=format&fit=crop" },
  { id: "zz", name: "ZZ Plant", price: 18.5, category: "Low Light",
    img: "https://images.unsplash.com/photo-1614594854191-1b9d35d4b2a3?q=80&w=800&auto=format&fit=crop" },
  { id: "peace-lily", name: "Peace Lily", price: 15.75, category: "Flowering",
    img: "https://images.unsplash.com/photo-1598899134739-24b4b8b5d4c2?q=80&w=800&auto=format&fit=crop" },
  { id: "aloe", name: "Aloe Vera", price: 11.25, category: "Succulents",
    img: "https://images.unsplash.com/photo-1528821154947-1aa3d7a0b17b?q=80&w=800&auto=format&fit=crop" },
];

const CATEGORIES = ["All", ...Array.from(new Set(PRODUCTS.map(p => p.category)))];
const currency = (n) => n.toLocaleString(undefined, { style: "currency", currency: "USD" });

// ---------- Cart Context ----------
const CartContext = createContext();
const useCart = () => useContext(CartContext);

function CartProvider({ children }) {
  const [items, setItems] = useState({}); // { productId: qty }
  const totalItems = useMemo(() => Object.values(items).reduce((a, b) => a + b, 0), [items]);
  const cartList = useMemo(
    () => Object.entries(items).map(([id, qty]) => ({ ...PRODUCTS.find(p => p.id === id), qty })),
    [items]
  );
  const totalCost = useMemo(() => cartList.reduce((s, p) => s + p.price * p.qty, 0), [cartList]);

  const add = (id) => setItems(prev => ({ ...prev, [id]: (prev[id] || 0) + 1 }));
  const remove = (id) => setItems(prev => {
    const q = (prev[id] || 0) - 1; const next = { ...prev };
    if (q <= 0) delete next[id]; else next[id] = q; return next;
  });
  const del = (id) => setItems(prev => { const n = { ...prev }; delete n[id]; return n; });
  const clear = () => setItems({});

  return (
    <CartContext.Provider value={{ items, cartList, totalItems, totalCost, add, remove, del, clear }}>
      {children}
    </CartContext.Provider>
  );
}

// ---------- Layout ----------
function Header() {
  const { totalItems } = useCart();
  return (
    <header className="header">
      <div className="container header__inner">
        <Link to="/" className="brand">
          <Leaf size={22} /> <span>Paradise Nursery</span>
        </Link>
        <nav className="nav">
          <Link to="/products" className="nav__link">Products</Link>
          <Link to="/cart" className="nav__link cartlink">
            <ShoppingCart size={18} />
            <span>Cart</span>
            <span className="badge">{totalItems}</span>
          </Link>
        </nav>
      </div>
    </header>
  );
}

function Page({ children }) {
  return <div className="page">{children}</div>;
}

function Container({ children }) {
  return <div className="container">{children}</div>;
}

// ---------- Pages ----------
function Landing() {
  const navigate = useNavigate();
  return (
    <Page>
      <Header />
      <section className="hero" style={{
        backgroundImage:
          "url(https://images.unsplash.com/photo-1519681393784-d120267933ba?q=80&w=1600&auto=format&fit=crop)"
      }}>
        <div className="hero__shade">
          <Container>
            <div className="hero__content">
              <motion.h1 initial={{opacity:0,y:16}} animate={{opacity:1,y:0}} transition={{duration:.5}}
                className="hero__title">Bring nature home.</motion.h1>
              <motion.p initial={{opacity:0,y:16}} animate={{opacity:1,y:0}} transition={{delay:.1,duration:.5}}
                className="hero__text">
                Paradise Nursery curates easy-care houseplants that thrive in real homes. We deliver fresh, healthy plants with care guides included.
              </motion.p>
              <motion.div initial={{opacity:0,y:16}} animate={{opacity:1,y:0}} transition={{delay:.2,duration:.5}}>
                <button className="btn btn--primary" onClick={() => navigate("/products")}>Get Started</button>
              </motion.div>
            </div>
          </Container>
        </div>
      </section>
    </Page>
  );
}

function Products() {
  const { add } = useCart();
  const [category, setCategory] = useState("All");
  const visible = PRODUCTS.filter(p => category === "All" || p.category === category);

  return (
    <Page>
      <Header />
      <Container>
        <div className="section">
          <h2 className="title">Shop plants</h2>
          <p className="muted">Browse by category and add your favorites to the cart.</p>

          <div className="chiprow">
            {CATEGORIES.map(c => (
              <button key={c}
                onClick={() => setCategory(c)}
                className={`chip ${category === c ? "chip--active" : ""}`}>
                {c}
              </button>
            ))}
          </div>

          <div className="grid">
            {visible.map(p => (
              <motion.div key={p.id} initial={{opacity:0,y:12}} animate={{opacity:1,y:0}} className="card">
                <img src={p.img} alt={p.name} className="card__img" />
                <div className="card__body">
                  <div className="card__head">
                    <div>
                      <h3 className="card__title">{p.name}</h3>
                      <p className="muted small">{p.category}</p>
                    </div>
                    <span className="price">{currency(p.price)}</span>
                  </div>
                  <button className="btn btn--primary btn--full" onClick={() => add(p.id)}>Add to Cart</button>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </Container>
    </Page>
  );
}

function Cart() {
  const { cartList, totalItems, totalCost, add, remove, del, clear } = useCart();
  const navigate = useNavigate();

  return (
    <Page>
      <Header />
      <Container>
        <div className="section grid-2">
          <div>
            <h2 className="title">Your cart</h2>
            <p className="muted">{totalItems} item{totalItems !== 1 ? "s" : ""} total</p>

            {cartList.length === 0 ? (
              <div className="panel">
                <p>Your cart is empty.</p>
                <button className="btn btn--primary" onClick={() => navigate("/products")}>Continue shopping</button>
              </div>
            ) : (
              <div className="stack">
                {cartList.map(item => (
                  <div key={item.id} className="cartrow">
                    <img src={item.img} alt={item.name} className="cartrow__img" />
                    <div className="cartrow__main">
                      <div className="cartrow__top">
                        <div>
                          <h3 className="cartrow__title">{item.name}</h3>
                          <p className="muted small">{currency(item.price)} each</p>
                        </div>
                        <button className="iconbtn" onClick={() => del(item.id)} title="Remove">
                          <Trash2 size={18} />
                        </button>
                      </div>

                      <div className="cartrow__bottom">
                        <div className="qty">
                          <button onClick={() => remove(item.id)} className="qty__btn"><Minus size={16} /></button>
                          <span className="qty__val">{item.qty}</span>
                          <button onClick={() => add(item.id)} className="qty__btn"><Plus size={16} /></button>
                        </div>
                        <div className="cartrow__sum">{currency(item.qty * item.price)}</div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>

          <aside className="panel">
            <h3>Order summary</h3>
            <div className="row">
              <span>Items</span><span>{totalItems}</span>
            </div>
            <div className="row">
              <span>Total</span><span className="bold">{currency(totalCost)}</span>
            </div>
            <div className="vspace"></div>
            <div className="stack">
              <button className="btn btn--primary" onClick={() => alert("Checkout complete! (demo)")}>Checkout</button>
              <button className="btn" onClick={() => navigate("/products")}>Continue shopping</button>
              {totalItems > 0 && <button className="btn btn--danger" onClick={clear}>Clear cart</button>}
            </div>
          </aside>
        </div>
      </Container>
    </Page>
  );
}

// ---------- App ----------
export default function App() {
  return (
    <CartProvider>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Landing />} />
          <Route path="/products" element={<Products />} />
          <Route path="/cart" element={<Cart />} />
        </Routes>
      </BrowserRouter>
    </CartProvider>
  );
}
